.modal-validacion .drag-container {
  border: 1px solid grey;
  border-radius: 6px;
  height: 250px;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 5px;
}
.modal-validacion .button-container {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}
.modal-validacion .button-container button {
  border-radius: 20px;
  border: 1px solid #079434;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 110px;
}

.modal-validacion .button-container button:hover {
  background-color: #079434;
  border: none;
  color: white;
}
.modal-validacion .drag-container img {
  height: 220px !important;
}
