.tareo-container {
  display: grid;
  grid-template-columns: 80% 1fr;
  margin-bottom: 10px;
}

.tareo-datos {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.tareo-boton {
  display: flex;
  justify-content: flex-end;
}

.tareo-boton button {
  border-radius: 20px;
  border: 1px solid #079434;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 110px;
  height: 28px;
  margin-top: 3px;
}

.tareo-boton button:hover {
  background-color: #079434;
  border: none;
  color: white;
}
